html, body {
  margin: 0;
}


h3 {
  padding: 10px;
}

img {
  width: 100%;
  border-radius: 10px;
}

.container {
  margin: 0 auto;
  padding-top: 10px;
  padding-left: 25px;
  padding-right: 25px;
}

.Card {
  width: 236px;
  background-color: #f9f9f9;
  padding: 0px 10px 10px 10px;
  margin: 4px;
  border-radius: 10px;
}
.Card:hover {
  background-color: #b8b8b8;
}

.pin-text {
  padding: 10px 5px 20px 5px;
}
/* Header CSS Styles */

.header {
  height: 64px;
  border-bottom: 1px solid #e5e6e8;
  padding-left: 20px;
  padding-right: 20px;
}

.button-container {
  height: 64px;
  width: 100%;
}

.wrapper {
  float: right;
  margin: 0 5px 0 5px;
  display: flex;
  align-items: center;
  height: 100%;
}

button {
  height: 40px;
  border: none;
  padding: 0 10px 0 10px;
  border-radius: 15px;
  /* font style */
  font-size: 16px;
  font-weight: 700;
}

button:hover {
  background-color: #e5e6e8;
}
